@import '../../static/theme';

.fo-details {
  height: 100%;
  max-height: 100%;
  overflow: scroll;
  margin-left: 10px;
  padding: 10px;
  margin-top: 10px;
  // box-shadow: 0px 0px 2px 0px rgba(112,112,112,0.09)
  border: 1px solid rgba(112,112,112,0.1);
  border-radius: 2px;
  box-sizing: border-box;
  // width: 40%
  width: 500px;

  label {
    font-family: "Source Sans Pro";
    font-size: 12px;
    display: inline-block; } }

.fo-input-wrapper {
  display: flex;
  align-items: flex-end;
  width: 100%;
  flex: 1;

  &  select {
    -webkit-appearance: none;
    outline: none;
    border: 1px solid rgba(112,112,112,0.4);
    background: white;
    border-radius: 0;
    height: 28px;
    padding-left: 12px;
    width: 70px;
    margin-left: 10px; } }


.fo-input {
  display: flex;
  flex-direction: column;


  input {
    padding: .5em 1em;


    &:focus {
      outline: none; } } }

.optimize-route {
  display: block;
  outline: none;
  background: none;
  height: 30px;
  width: 200px;
  background: $second-primary-color-hex;
  color: white;
  border: none;
  border-radius: 2px;
  font-size: 10px;
  cursor: pointer;
  box-shadow: 0px 1px 12px 2px rgba(80,125,240,0.2);
  margin-left: auto; }

.file-upload {
  height: 200px;
  display: flex;
  justify-content: center;
  align-items: center;
  border: 1px dotted rgba(80,125,240,0.2);

  label {
    margin-right: 10px;
    display: block; }

  input {
    border: 1px solid pink; } }


.fuel-op-button {
  width: 100%;
  display: flex;
  margin-top: 20px; }
